// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import Vuex from 'vuex';

Vue.config.productionTip = false;
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    posts: [
      {
	id: 1,
	title: 'Welcome to Mango!',
	slug: '/post/1/Welcome-to-Mango',
	date: '11/10/2017, 10:06:21',
	content: '# Hey there!\n\nThis is Mango, a powerful, responsive and flexible blog platform designed for you, built using Vue.js, Lodash, and Marked. It\'s semi-static, which just means the HTML output is compiled upon load instead of just as a raw HTML file, this is in order to keep the size down and make it faster, smaller, and give more flexibility to the authors. It has a beautiful built-in post creator as well, you can enable this in the main.js file by setting ```devmode``` to true, instead of false.\n\n[Learn More](https://github.com/michaelgv/mangojs)',
      },
    ],
    papers: [
    ],
    contact: {
      enableSocialMedia: false,
      socialMedia: {
        instagram: '',
        facebook: '',
      },
      email: '',
      aboutme: 'Hello, world!',
    },
    devmode: false,
  },
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  store: store,
});



